
'use client';
import { useState, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Header } from '@/components/layout/Header';
import { useMockDatabase } from '@/lib/data';
import { Button } from '@/components/ui/button';
import { Card, CardHeader, CardTitle, CardDescription, CardContent, CardFooter } from '@/components/ui/card';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { useToast } from '@/hooks/use-toast';
import { Loader2, Plus, Save, Trash2, UserPlus, Users, X } from 'lucide-react';
import type { Family } from '@/types';
import { useAuth } from '@/hooks/use-auth';
import { useRouter } from 'next/navigation';
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle, AlertDialogTrigger } from '@/components/ui/alert-dialog';
import { Avatar, AvatarFallback } from '@/components/ui/avatar';


const addMemberSchema = z.object({
  name: z.string().min(2, 'Name must be at least 2 characters.'),
  email: z.string().email('Please enter a valid email.'),
});

type AddMemberFormValues = z.infer<typeof addMemberSchema>;

export default function MyFamilyPage() {
  const db = useMockDatabase();
  const { toast } = useToast();
  const { isLoggedIn, isAdmin, MOCK_VOLUNTEER_ID } = useAuth();
  const router = useRouter();
  
  const [family, setFamily] = useState<Family | null>(null);
  const [isAdding, setIsAdding] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [isLoading, setIsLoading] = useState(true);

  const form = useForm<AddMemberFormValues>({
    resolver: zodResolver(addMemberSchema),
    defaultValues: {
      name: '',
      email: '',
    },
  });

  const fetchFamily = async () => {
    if (isLoggedIn) {
        setIsLoading(true);
        const familyData = await db.getFamilyByVolunteerId(MOCK_VOLUNTEER_ID);
        setFamily(familyData);
        setIsLoading(false);
    }
  }

  useEffect(() => {
    if (!isLoggedIn) {
      router.push('/');
    } else if (isAdmin) {
      router.push('/admin');
    } else {
        fetchFamily();
    }
  }, [isLoggedIn, isAdmin, router, MOCK_VOLUNTEER_ID]);


  const onAddSubmit = async (data: AddMemberFormValues) => {
    if (!family) return;
    setIsSaving(true);
    try {
      await db.addFamilyMember(family.id, data.name, data.email);
      toast({ title: 'Family Member Added', description: `${data.name} has been added to your family.` });
      form.reset();
      setIsAdding(false);
      await fetchFamily(); // Refetch to update list
    } catch (e) {
      toast({ variant: 'destructive', title: 'Error', description: 'Could not add family member.' });
    } finally {
      setIsSaving(false);
    }
  };

  const handleRemoveMember = async (volunteerId: string, name: string) => {
      if (!family) return;
      try {
          await db.removeFamilyMember(family.id, volunteerId);
          toast({ title: 'Member Removed', description: `${name} has been removed from your family.` });
          await fetchFamily();
      } catch (e) {
          toast({ variant: 'destructive', title: 'Error', description: 'Could not remove member.' });
      }
  }

  if (!isLoggedIn || isAdmin) {
    return null; // Or a loading spinner
  }

  return (
    <div className="flex min-h-screen w-full flex-col bg-muted/40">
      <Header />
      <main className="flex-1 p-4 md:p-8">
        <div className="max-w-3xl mx-auto space-y-6">
          <div>
            <h1 className="text-3xl font-bold font-headline tracking-tight">My Family</h1>
            <p className="text-muted-foreground mt-1">
              Manage family members you serve with. This helps with scheduling and availability.
            </p>
          </div>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between">
                <div>
                    <CardTitle>Family Members</CardTitle>
                    <CardDescription>The list of volunteers in your family group.</CardDescription>
                </div>
                 {!isAdding && (
                    <Button onClick={() => setIsAdding(true)}>
                        <UserPlus className="mr-2 h-4 w-4" /> Add Member
                    </Button>
                )}
            </CardHeader>
            <CardContent>
                {isLoading ? (
                    <div className="flex justify-center p-8">
                        <Loader2 className="h-8 w-8 animate-spin text-muted-foreground" />
                    </div>
                ) : family ? (
                    <ul className="space-y-3">
                        {family.members.map(member => (
                            <li key={member.id} className="flex items-center justify-between p-3 rounded-md bg-muted">
                                <div className="flex items-center gap-4">
                                    <Avatar>
                                        <AvatarFallback>{member.name.charAt(0)}</AvatarFallback>
                                    </Avatar>
                                    <div>
                                        <p className="font-semibold">{member.name}</p>
                                        <p className="text-sm text-muted-foreground">{member.isPrimary ? 'Primary Contact (You)' : 'Family Member'}</p>
                                    </div>
                                </div>
                                {!member.isPrimary && (
                                     <AlertDialog>
                                        <AlertDialogTrigger asChild>
                                            <Button variant="ghost" size="icon">
                                                <Trash2 className="h-4 w-4 text-destructive" />
                                            </Button>
                                        </AlertDialogTrigger>
                                        <AlertDialogContent>
                                            <AlertDialogHeader>
                                                <AlertDialogTitle>Are you sure?</AlertDialogTitle>
                                                <AlertDialogDescription>
                                                    This will remove {member.name} from your family group. This action cannot be undone.
                                                </AlertDialogDescription>
                                            </AlertDialogHeader>
                                            <AlertDialogFooter>
                                                <AlertDialogCancel>Cancel</AlertDialogCancel>
                                                <AlertDialogAction onClick={() => handleRemoveMember(member.id, member.name)} className="bg-destructive hover:bg-destructive/90 text-destructive-foreground">Remove</AlertDialogAction>
                                            </AlertDialogFooter>
                                        </AlertDialogContent>
                                    </AlertDialog>
                                )}
                            </li>
                        ))}
                    </ul>
                ) : (
                    <div className="text-center p-8 border-2 border-dashed rounded-lg">
                        <Users className="mx-auto h-12 w-12 text-muted-foreground" />
                        <h3 className="mt-4 text-lg font-medium">No Family Members Yet</h3>
                        <p className="mt-1 text-sm text-muted-foreground">Click "Add Member" to start building your family group.</p>
                    </div>
                )}
            </CardContent>
          </Card>

          {isAdding && (
            <Card>
                <CardHeader>
                    <CardTitle>Add New Family Member</CardTitle>
                    <CardDescription>Enter their details below.</CardDescription>
                </CardHeader>
                <CardContent>
                    <Form {...form}>
                        <form onSubmit={form.handleSubmit(onAddSubmit)} className="space-y-4">
                            <FormField
                                control={form.control}
                                name="name"
                                render={({ field }) => (
                                <FormItem>
                                    <FormLabel>Full Name</FormLabel>
                                    <FormControl>
                                    <Input placeholder="John Doe" {...field} />
                                    </FormControl>
                                    <FormMessage />
                                </FormItem>
                                )}
                            />
                             <FormField
                                control={form.control}
                                name="email"
                                render={({ field }) => (
                                <FormItem>
                                    <FormLabel>Email Address</FormLabel>
                                    <FormControl>
                                    <Input placeholder="john@example.com" {...field} />
                                    </FormControl>
                                    <FormMessage />
                                </FormItem>
                                )}
                            />
                             <CardFooter className="px-0 pt-6 flex justify-end gap-2">
                                <Button type="button" variant="ghost" onClick={() => setIsAdding(false)} disabled={isSaving}>Cancel</Button>
                                <Button type="submit" disabled={isSaving}>
                                    {isSaving ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Save className="mr-2 h-4 w-4" />}
                                    Save
                                </Button>
                            </CardFooter>
                        </form>
                    </Form>
                </CardContent>
            </Card>
          )}

        </div>
      </main>
    </div>
  );
}
